body {
  background-color: #222;
  font: 16px/1.2 "Lucida Console";
}
.App {
  text-align: center;
  background-color: #222;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 97vh;
}
.App-header {
  background-color: #222;
  width: 100%;
  padding: 10px;
  color: white;
  text-align: left;
  -ms-flex: 2 1;
      flex: 2 1;
  -ms-flex-item-align: start;
      align-self: flex-start
}
.App-body {
  background-color: #888;
  width: 100%;
  -ms-flex: 10 1;
      flex: 10 1;
  -ms-flex-item-align: center;
      align-self: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.App-footer {
  background: #222;
  width: 100%;
  font-size: .6em;
  padding: 10px;
  width: 100%;
  color: white;
  text-align: left;
}
.lft {
  -ms-flex-order: 1;
      order: 1;
  width: 40%;
}
.cntr {
  -ms-flex-order: 2;
      order: 2;
  width: 50%;
}
.rght {
  -ms-flex-order: 3;
      order: 3;
  width: 10%;
}

.lft-header, .rght-header {
  background-color: #222;
  overflow: hidden;
  height: 5vh;
}

.topnav {
  background-color: #222;
  overflow: hidden;
  height: 5vh;
  padding-left: 6vw;
}
.navButton {
  float: left;
  display: block;
  color: white;
  background-color: #222;
  border: 1px solid white;
  border-radius: 5px;
  text-align: center;
  padding: .4em .8em .4em .8em;
  margin: .4em;
  text-decoration: none;
  font: 8px/1.2 "lucida console";
  -webkit-transition: background-color .2s;
  -o-transition: background-color .2s;
  transition: background-color .2s;
}
.navButton:hover {
  color: black;
  border: 1px solid black;
  background-color: white;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}



@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.textContent {
  padding: 15px;
  font-size: .8em;
  text-align: left;
  margin-left: 4vw;
}



